import React from 'react';
import Button from 'react-bootstrap/Button';
import {observer} from "mobx-react";
import Switch from "react-router-dom/es/Switch";
import Route from "react-router-dom/es/Route";
import {withRouter} from "react-router";
import Form from "react-bootstrap/Form";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import VehicleStore from "./stores/VehicleStore";
import './App.css';

const vehicleStore = new VehicleStore();

const handleSelect = withRouter(({history, match}) => {
    console.log(history);
    console.log(match);
    history.push('/'+ match.params.text)
});

function NavTabs() {
    return (
        <Tabs defaultActiveKey="home" id="uncontrolled-tab-example">
            <Tab eventKey="home" title="Home" onClick={handleSelect}>
                <h1>Homescreen</h1>
            </Tab>
            <Tab eventKey="profile" title="Profile" onSelect={handleSelect}>
                <NewVehicle/>
            </Tab>
            <Tab eventKey="contact" title="Contact" component={handleSelect} disabled>
                <NewVehicle/>
            </Tab>
        </Tabs>
    );
}

function App() {
    return (
        <div>
            <container>
                <img src="https://i.imgur.com/bVMYcYR.png" className="Logo" alt="NoFoss Logo"/>
            </container>
            <container>
                <Switch>
                    <Route path={"/admin"} component={Admin}/>
                    <Route exact path={"/"} render={()=><NavTabs/>}/>
                    <Route render={()=><h1>404</h1>}/>
                </Switch>
            </container>
        </div>
    );
}

const NewVehicle = withRouter(({history, match}) => {
    console.log(history);
    console.log(match);
    return (
        <Form>
            <Form.Group controlId="exampleForm.ControlInput1">
                <Form.Label>Email address</Form.Label>
                <Form.Control type="email" placeholder="name@example.com"/>
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlSelect1">
                <Form.Label>Example select</Form.Label>
                <Form.Control as="select">
                    <option>1</option>
                    <option>2</option>
                    <option>3</option>
                    <option>4</option>
                    <option>5</option>
                </Form.Control>
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlSelect2">
                <Form.Label>Example multiple select</Form.Label>
                <Form.Control as="select" multiple>
                    <option>1</option>
                    <option>2</option>
                    <option>3</option>
                    <option>4</option>
                    <option>5</option>
                </Form.Control>
            </Form.Group>
            <Form.Group controlId="exampleForm.ControlTextarea1">
                <Form.Label>Example textarea</Form.Label>
                <Form.Control as="textarea" rows="3"/>
            </Form.Group>
        </Form>
    )
});

const Admin = withRouter(({history, match}) => {
    console.log(history);
    console.log(match);
    return <div>
        <h1>About {match.params.text}</h1>
        <Button onClick={() => history.push("/")}>Go to front</Button>
    </div>
});

export default observer(App);